<template>
  <div id="calendarEvent">
    <!-- headerBox -->
    <HeaderBox
      @searchHandlerDate="searchHandlerDate"
      @changeHandler="changeHandler"
      @exportHandler="exportHandler"
      @importHandler="importHandler"
      @handleAddOrEdit="handleAddOrEdit"
      @getSearchDate="getSearchDate"
      :buttonList="buttonList"
      :showDatePicker="true"
      :isEvent="true"
      :searchLoading="searchLoading"
    ></HeaderBox>

    <!-- mainTable -->
    <div class="mainTable" v-if="eventsData">
      <div class="tableContainer mt-5">
        <el-table
          header-cell-class-name="tableHeader"
          empty-text=""
          :data="eventsData"
          style="width: 100% ;overflow:auto"
          :default-sort="{ prop: 'date', order: 'descending' }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column width="160" prop="EventName" label=" / " sortable></el-table-column>
          <el-table-column width="130" prop="EventTypeName" label="" sortable></el-table-column>
          <el-table-column width="200" prop="ShowStartDate" label="" sortable>
            <template slot-scope="scope">
              <div class="showDateBox">
                <span class="dateFz">{{ scope.row.ShowStartDate }}</span>
                <div class="dateLine"></div>
                <span class="dateFz">{{ scope.row.ShowEndDate }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column width="200" prop="EventStartDate" label=" / " sortable>
            <template slot-scope="scope">
              <div class="eventDateBox">
                <span class="dateFz">{{ scope.row.EventStartDate }}</span>
                <div class="dateLine"></div>
                <span class="dateFz">{{ scope.row.EventEndDate }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="EventAddr" label="" sortable>
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" :open-delay="500" placement="top-start">
                <div slot="content">
                  <span>{{ scope.row.EventAddr }}</span>
                </div>
                <p class="textOverflow">
                  <span>{{ scope.row.EventAddr }}</span>
                </p>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column width="220" prop="emit" label="">
            <template slot-scope="scope">
              <el-button
                v-if="hasBtn('btnEdit')"
                class="outline"
                size="mini"
                @click="copyHandler(scope.row)"
              ></el-button>
              <el-button
                v-if="hasBtn('btnEdit')"
                class="outline"
                size="mini"
                @click="handleAddOrEdit('edit', scope.row)"
              ></el-button>
              <el-button
                v-if="hasBtn('btnDelete')"
                type="danger"
                class="outline"
                size="mini"
                @click="deleteHandler(scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- <ckeditor :editor="editor" v-model="inputDescription" :config="editorConfig"></ckeditor> -->
    <!-- pagination -->
    <Pagination
      v-if="totalCount"
      :keyWord="keyWordInput"
      :start="startDate"
      :end="endDate"
      :total="totalCount"
      :page-size="pageSize"
      :currentPage="currentPage"
      @changePage="getEvents"
    ></Pagination>

    <!-- addOrEditDialog -->

    <el-dialog :close-on-click-modal="false"
      :title="addOrEdit"
      :visible.sync="addOrEditDialog"
      v-if="unitsData"
      custom-class="addOrEditDialog"
    >
      <ValidationObserver ref="obs">
        <!-- <el-scrollbar class="scrollbar-handle" ref="scrollBox"> -->
        <div class="inputBox" style="margin-top: 3rem;">
          <div class="inputTitle"> / </div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-input
              :class="classes"
              type="textarea"
              :rows="2"
              style="width:550px"
              v-model="eventNameInput"
              placeholder=" / "
            ></el-input>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"> / </div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <div class="personalContainer mt-5 ck" ref="ck">
              <ckeditor
                :class="classes"
                :editor="editor"
                v-model="inputDescription"
                :config="editorConfig"
              ></ckeditor>
            </div>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-select
              :class="classes"
              v-if="eventTypeData"
              v-model="eventCategorySelete"
              placeholder=""
            >
              <el-option
                v-for="type in eventTypeData"
                :key="type.Value"
                :label="type.Text"
                :value="type.Value"
              ></el-option>
            </el-select>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-date-picker
              :class="classes"
              v-model="showDate"
              type="datetimerange"
              range-separator="~"
              start-placeholder=""
              end-placeholder=""
              placeholder=""
            ></el-date-picker>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"> / </div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-date-picker
              :class="classes"
              v-model="eventDate"
              type="datetimerange"
              range-separator="~"
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"> / </div>
          <ValidationProvider name=" / !!" rules="required" v-slot="{  errors,classes }">
            <el-input
              :class="classes"
              style="width:600px"
              v-model="eventSiteInput"
              placeholder=" / "
            ></el-input>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <ValidationProvider name=" / !!" rules="required" v-slot="{  errors,classes }">
            <el-input
              :class="classes"
              style="width:600px"
              v-model="eventUrlInput"
              placeholder=" / "
            ></el-input>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-select
              filterable
              no-match-text=""
              :class="classes"
              v-if="userControlUnit"
              v-model="unitSelete"
              placeholder=""
            >
              <el-option
                v-for="unit in userControlUnit"
                :key="unit.UntId"
                :label="unit.UntNameFull"
                :value="unit.UntId"
              ></el-option>
            </el-select>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <el-upload
            ref="upload"
            class="upload-demo"
            :action="$store.state.apiBase+'Img'"
            list-type="text"
            :headers="uploadHeader"
            :on-success="successUpload"
          >
            <el-tooltip :open-delay="500" class="item" effect="dark" placement="top-start">
              <div slot="content">
                :doc/docx/xls/xlsx/ppt/pttx/pdf/jpg/png
                <br />:10MB
              </div>
              <el-button size="small" type="primary"></el-button>
            </el-tooltip>
          </el-upload>
        </div>

        <div class="inputBox">
          <div class="inputTitle"></div>
          <p class="noFiles" v-if="uploadUrl.length === 0 "></p>

          <div v-if="uploadUrl">
            <el-button
              v-for="(url,index) in uploadUrl"
              :key="index"
              class="downloadBtn"
              size="mini"
            >
              <el-tooltip
                :open-delay="500"
                class="item"
                effect="dark"
                :content="fileName(url)"
                placement="top-start"
              >
                <p class="fileName" v-if="url">
                  <i @click.capture="delFile(index)" class="fas fa-trash-alt"></i>
                  {{fileName(url)}}
                </p>
              </el-tooltip>
            </el-button>
          </div>
        </div>

        <div class="inputBox" style="align-items:flex-start">
          <div class="inputTitle"></div>
          <div>
            <el-checkbox class="relatedCheck" v-model="isRelated"></el-checkbox>
            <div class="selectBox">
              <el-select
                filterable
                no-match-text=""
                @change="lv1Change"
                class="unitSelect"
                v-model="unit1"
                placeholder=""
              >
                <el-option
                  :value="unit.UntId"
                  :label="unit.UntNameFull"
                  v-for="unit in unitLv1"
                  :key="unit.UntId"
                >{{unit.UntNameFull}}</el-option>
              </el-select>
              <el-select
                filterable
                no-match-text=""
                @change="lv2Change"
                class="unitSelect"
                v-model="unit2"
                placeholder=""
                v-if="unit1 && unitLv2.length > 0"
              >
                <el-option
                  v-for="unit in unitLv2"
                  :key="unit.UntId"
                  :value="unit.UntId"
                  :label="unit.UntNameFull"
                >{{unit.UntNameFull}}</el-option>
              </el-select>
              <el-select
                filterable
                no-match-text=""
                class="unitSelect"
                @change="lv3Change"
                v-model="unit3"
                placeholder=""
                v-if="unit2 && unitLv3.length > 0"
              >
                <el-option
                  v-for="unit in unitLv3"
                  :key="unit.UntId"
                  :value="unit.UntId"
                  :label="unit.UntNameFull"
                >{{unit.UntNameFull}}</el-option>
              </el-select>
            </div>
          </div>
        </div>

        <div class="inputBox" style="align-items:flex-start">
          <div class="inputTitle"></div>
          <div class="selectBox">
            <el-select
              filterable
              no-match-text=""
              :loading="userNameLoading"
              loading-text="Loading..."
              no-data-text=""
              v-model="userNameSelect"
              placeholder=""
              @change="setUser"
            >
              <el-option
                v-for="user in usersData"
                :key="user.Account"
                :value="user.Account"
                :label="user.Name"
              ></el-option>
            </el-select>
            <el-select v-model="memberTitle" placeholder="">
              <el-option :value="userData.Title">{{userData.Title}}</el-option>
            </el-select>
            <el-select v-model="rloeSelect" placeholder="Role">
              <el-option v-for="role in rolesData" :key="role" :value="role"></el-option>
            </el-select>
          </div>
          <el-button :loading="addLoading" @click="addToTable"></el-button>
        </div>
        <div class="inputBox" style="align-items:flex-start">
          <div class="inputTitle"></div>
          <el-table
            header-cell-class-name="tableHeader"
            v-if="usersTableData"
            empty-text=""
            :data="usersTableData"
            style="width: 100%"
          >
            <el-table-column prop="userName" label="" width="180"></el-table-column>
            <el-table-column prop="usertitle" label="" width="180"></el-table-column>
            <el-table-column prop="unit" label=""></el-table-column>
            <el-table-column prop label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="danger"
                  @click.native.prevent="deleteRow(scope.$index, usersTableData)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- </el-scrollbar> -->
      </ValidationObserver>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addOrEditDialog = false"> </el-button>
        <el-button v-if="addOrEdit===''" type="primary" @click="addHandler"> </el-button>
        <el-button v-if="addOrEdit===''" type="primary" @click="editHandler"> </el-button>
        <el-button v-if="addOrEdit===''" type="primary" @click="addHandler"> </el-button>
      </div>
    </el-dialog>

    <!-- changeDialog -->

    <el-dialog :close-on-click-modal="false" title="" custom-class="changeDialog" :visible.sync="changeDialog">
      <ValidationObserver ref="obs2">
        <div class="changeInputBox">
          <p class="changeInputTitle"></p>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-select
              filterable
              :class="classes"
              no-match-text=""
              no-data-text=""
              v-model="changeMember"
              placeholder=""
            >
              <el-option
                v-for="(member,index) in changeMemberData"
                :key="`${member + index}`"
                :value="member"
              ></el-option>
            </el-select>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>

        <div class="changeInputBox">
          <p class="changeInputTitle"></p>
          <ValidationProvider name="!!" rules="required" v-slot="{  errors,classes }">
            <el-autocomplete
              class="inline-input"
              :class="classes"
              v-model="newChangeMember"
              :fetch-suggestions="querySearch"
              placeholder=""
              :trigger-on-focus="false"
            ></el-autocomplete>
            <span class="validateSpan" v-if="errors[0]">{{ errors[0] }}</span>
          </ValidationProvider>
        </div>
      </ValidationObserver>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeDialog = false"> </el-button>
        <el-button type="primary" @click="confirmChange"> </el-button>
      </span>
    </el-dialog>

    <!-- importDialog -->
    <el-dialog :close-on-click-modal="false" title="" :visible.sync="importDialogVisible" width="30%">
      <el-upload
        ref="import"
        class="upload-demo"
        :multiple="false"
        action="https://scan.1966.org.tw/api/CalendarEvent/ImportExcel/xlsx"
        list-type="text"
        :headers="uploadHeader"
        :before-upload="beforeAvatarUpload"
        :on-success="successImport"
        :on-error="errorImport"
        :auto-upload="false"
        :limit="1"
        :on-exceed="importExceed"
      >
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip"> xlsx  xls </div>
        <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="importDialogVisible = false"> </el-button>
        <el-button type="primary" @click="confireImport"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import HeaderBox from "../../components/HeaderBox";
import Pagination from "../../components/Pagination";
import moment, { duration } from "moment";
// ck
import ClassicEditor from "@ckeditor/ckeditor5-editor-classic/src/classiceditor";
import EssentialsPlugin from "@ckeditor/ckeditor5-essentials/src/essentials";
import BoldPlugin from "@ckeditor/ckeditor5-basic-styles/src/bold";
import ItalicPlugin from "@ckeditor/ckeditor5-basic-styles/src/italic";
import LinkPlugin from "@ckeditor/ckeditor5-link/src/link";
import ParagraphPlugin from "@ckeditor/ckeditor5-paragraph/src/paragraph";
import Alignment from "@ckeditor/ckeditor5-alignment/src/alignment";
import Heading from "@ckeditor/ckeditor5-heading/src/heading.js";
import FontBackgroundColor from "@ckeditor/ckeditor5-font/src/fontbackgroundcolor.js";
import FontColor from "@ckeditor/ckeditor5-font/src/fontcolor.js";
import FontFamily from "@ckeditor/ckeditor5-font/src/fontfamily.js";
import FontSize from "@ckeditor/ckeditor5-font/src/fontsize.js";
import MediaEmbed from "@ckeditor/ckeditor5-media-embed/src/mediaembed.js";
import List from "@ckeditor/ckeditor5-list/src/list.js";
import Image from "@ckeditor/ckeditor5-image/src/image.js";
import ImageCaption from "@ckeditor/ckeditor5-image/src/imagecaption.js";
import ImageResize from "@ckeditor/ckeditor5-image/src/imageresize.js";
import ImageStyle from "@ckeditor/ckeditor5-image/src/imagestyle.js";
import ImageToolbar from "@ckeditor/ckeditor5-image/src/imagetoolbar.js";
import ImageUpload from "@ckeditor/ckeditor5-image/src/imageupload.js";
import CKFinder from "@ckeditor/ckeditor5-ckfinder/src/ckfinder";
export default {
  name: "CalendarEvents",
  components: {
    HeaderBox,
    Pagination
  },
  data() {
    return {
      // ck
      editor: ClassicEditor,
      editorData: "",
      editorConfig: {
        plugins: [
          EssentialsPlugin,
          BoldPlugin,
          ItalicPlugin,
          LinkPlugin,
          ParagraphPlugin,
          Alignment,
          Heading,
          FontBackgroundColor,
          FontColor,
          FontFamily,
          FontSize,
          MediaEmbed,
          List,
          Image,
          ImageResize,
          ImageUpload,
          ImageToolbar,
          ImageCaption,
          ImageStyle,
          CKFinder
        ],

        toolbar: {
          items: [
            "heading",
            "|",
            "bold",
            "italic",
            "|",
            "fontBackgroundColor",
            "fontColor",
            "fontSize",
            "|",
            "link",
            "imageUpload",
            "mediaEmbed",
            "|",
            "alignment",
            "numberedList",
            "|",
            "undo",
            "redo"
          ]
        },
        image: {
          toolbar: [
            "imageTextAlternative",
            "|",
            "imageStyle:full",
            "imageStyle:side"
          ]
        },
        ckfinder: {
          uploadUrl: `https://scan.1966.org.tw/images/Upload/Pic`,
          //  API 
          options: {
            resourceType: "Images"
            // 
          }
        }
      },

      userNameLoading: false,
      // 
      eventsData: "",
      eventTypeData: "",
      unitsData: "",
      usersData: "",
      rolesData: [
        "/",
        "",
        "",
        "",
        "/",
        "/",
        "()",
        "",
        "",
        "",
        "()",
        "",
        "",
        "()",
        "",
        "()",
        "",
        "",
        ""
      ],
      userData: "",
      usersTable: [],
      allMembersData: "",

      //
      eventNameInput: "",
      inputDescription: "",
      eventCategorySelete: "",
      showDate: [],
      eventDate: [],
      eventSiteInput: "",
      eventUrlInput: "",
      unitSelete: "",
      uploadUrl: "",
      unit1: "",
      unit2: "",
      unit3: "",
      userNameSelect: "",
      rloeSelect: "",
      isRelated: true,
      memberTitle: "",

      // 
      eventIds: "",
      changeMemberData: [],
      changeMember: "",
      newChangeMember: "",
      allMembersData: "",

      // copy
      copyEventId: "",

      //
      searchLoading: false,
      addLoading: false,
      totalCount: "",
      pageSize: "",
      currentPage: 1,
      addOrEdit: "",
      editEventId: "",
      startDate: "",
      endDate: "",
      keyWordInput: "",
      uploadHeader: {
        Authorization: `Bearer ${window.localStorage.getItem("Token")}`
      },
      member: "",
      buttonList: [],
      addOrEditDialog: false,
      importDialogVisible: false,
      changeDialog: false,
      searchDate: ""
    };
  },
  computed: {
    unitLv1() {
      const vm = this;
      return vm.unitsData.filter(unit => {
        return unit.UntLevelb === "1";
      });
    },
    unitLv2() {
      const vm = this;
      let arrLv1 = vm.unitsData.filter(unit => {
        return unit.UntId === vm.unit1;
      });
      let uintLv1 = arrLv1[0];
      return vm.unitsData.filter(unit => {
        return unit.UntIdUp === uintLv1.UntId && unit.UntLevelb === "2";
      });
    },
    unitLv3() {
      const vm = this;
      let arrLv2 = vm.unitsData.filter(unit => {
        return unit.UntId === vm.unit2;
      });
      let uintLv2 = arrLv2[0];
      return vm.unitsData.filter(unit => {
        return unit.UntIdUp === uintLv2.UntId && unit.UntLevelb === "3";
      });
    },
    usersTableData() {
      return this.usersTable;
    },
    userControlUnit() {
      const vm = this;
      let userCtrl = JSON.parse(window.localStorage.getItem("user")).CtrlUnits;
      let arr = userCtrl.map(uc => {
        return vm.unitsData.filter(unit => {
          return unit.UntId === uc;
        })[0];
      });
      return arr;
    }
  },
  methods: {
    onReady(editor) {
      let ck = this.$refs.ck;
      console.log(ck);
      this.$refs.ck.prepend(editor.ui.view.toolbar.element);
    },
    beforeUpload(file) {
      console.log(file);
      return false;
    },
    async getUnits() {
      const vm = this;
      await vm.$api.GetUnits().then(res => {
        vm.unitsData = res.data;
      });
    },
    getEvents(page = 1, key, startDate, endDate) {
      const vm = this;
      let params = {
        key: vm.keyWordInput,
        page,
        startDate: vm.startDate,
        endDate: vm.endDate
      };
      vm.$api.GetEventsPage(params).then(res => {
        console.log(res);
        vm.totalCount = res.data.response.dataCount;
        vm.pageSize = res.data.response.PageSize;
        vm.currentPage = page;
        let arr = res.data.response.data;
        arr.map(event => {
          event.EventEndDate = moment(event.EventEndDate).format(
            "YYYY-MM-DD HH:mm"
          );
          event.EventStartDate = moment(event.EventStartDate).format(
            "YYYY-MM-DD HH:mm"
          );
          event.ShowEndDate = moment(event.ShowEndDate).format(
            "YYYY-MM-DD HH:mm"
          );
          event.ShowStartDate = moment(event.ShowStartDate).format(
            "YYYY-MM-DD HH:mm"
          );
        });
        vm.eventsData = arr;
        vm.$store.dispatch("loadingHandler", false);
      });
    },
    getEventType() {
      let user = JSON.parse(window.localStorage.user);
      this.eventTypeData = user.DropCtrlTypes;
    },
    getAllUser() {
      const vm = this;
      let arr = [];
      vm.$api.GetUsers({ unitCode: "" }).then(res => {
        vm.allMembersData = res.data.map(u => {
          u.value = u.Name;
          return u;
        });
      });
    },
    lv1Change() {
      const vm = this;
      vm.unit2 = "";
      vm.unit3 = "";
      vm.userNameSelect = "";
      let unitCode = vm.unit1;
      let params = {
        unitCode
      };
      vm.getUsers(params);
    },
    lv2Change() {
      const vm = this;
      vm.unit3 = "";
      vm.userNameSelect = "";
      let unitCode = vm.unit2;
      let params = {
        unitCode
      };
      vm.getUsers(params);
    },
    lv3Change() {
      const vm = this;
      vm.userNameSelect = "";
      let unitCode = vm.unit3;
      let params = {
        unitCode
      };
      vm.getUsers(params);
    },
    setUser() {
      const vm = this;
      let id = vm.userNameSelect;
      let userArr = vm.usersData.filter(user => {
        return user.Account === id;
      });
      vm.userData = userArr[0];
      console.log(vm.userData);
    },
    getUsers(params) {
      const vm = this;
      vm.userNameLoading = true;
      if (!vm.isRelated) {
        let params1 = {
          unitCode: ""
        };
        vm.$api.GetUsers(params1).then(res => {
