    exportHandler({ key, startDate, endDate }) {
      const vm = this;
      let date = moment(new Date()).format("YYYYMMDDHHMM");
      let sed;
      let keytxt;
      if (!startDate) {
        sed = "";
      } else {
        sed = `${startDate} ~ ${endDate}`;
      }
      if (!key) {
        keytxt = "";
      } else {
        keytxt = `${key}`;
      }
      vm.$swal({
        title: "",
        html: `${sed}${keytxt} <br /><p class='fontRed'>**</p>`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#2f3e52",
        cancelButtonColor: "#522f2f",
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(result => {
        if (result.value) {
          let config = {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${vm.$store.state.token}`
            },
            responseType: "blob" ////  blob
          };
          this.$http
            .get(
              `https://scan.1966.org.tw/api/CalendarEvent/GetCalendarExcel?key=${key}&startDate=${startDate}&endDate=${endDate}`,
              config
            )
            .then(res => {
              console.log(res);
              let blob = new Blob([res.data], {
                type: "application/" + res.headers["content-type"]
              });
              let downloadElement = document.createElement("a");
              let href = window.URL.createObjectURL(blob); // 
              downloadElement.href = href;
              downloadElement.download = `_${date}.xlsx`; // 
              // 
              document.body.appendChild(downloadElement);
              downloadElement.click(); // 
              document.body.removeChild(downloadElement); // 
              window.URL.revokeObjectURL(href); //  blob 
              vm.$alertT.fire({
                icon: "success",
                title: ``
              });
            });
        } else {
          vm.$alertT.fire({
            icon: "info",
            title: ``
          });
        }
      });
